.edit {
    padding: 10px;
    width: 350px;
    margin: 0 auto;
    top: 5vh;
    box-shadow:
        rgba(22, 31, 39, 0.42) 0px 60px 123px -25px,
        rgba(19, 26, 32, 0.08) 0px 35px 75px -35px;
    border-radius: 10px;
    border: 1px solid;
    border-color:
        rgb(213, 220, 226) rgb(213, 220, 226) rgb(184, 194, 204);
    background-color: $clr3;
    opacity: .9;
    position: relative;
    color: #222;
    font-weight: 500;

    @include for-tablet-layout {
        width: 70%;
    }

    @include for-mobile-layout {
        width: 80%;
    }

    @include for-mini-layout {
        width: 90%;
    }

    .back-btn {
        width: 25px;
        cursor: pointer;
    }

    form {
        gap: 10px;

        button {
            bottom: 13px;
            border-radius: 8px;
            background-color: $clr5;
            border: none;
            font-size: 13px;
            font-family: system-ui;
            font-weight: 620;
            padding: 7px;
            margin-top: 5px;
        }

        input {
            height: 30px;
            width: 80%;
            border-radius: 4px;
            border: 2px solid grey;
        }

        label {
            margin-bottom: 2px;
        }
        select{
            width: 150px;

        }


    }

    h2 {
        text-align: center;
    
    }

}